import styled from "styled-components";

import Button from "../../../components/Button";
import IconLoader from "../../../components/IconLoader";
import {useState} from "react";
import EmailModal from "../modals/EmailModal";

const GetEmail = () => {
  const [showEmailModal, setShowEmailModal] = useState<boolean>(false);


  return (
    <Container>
      <EmailModal
        openModal={showEmailModal}
        onModalClose = {() => setShowEmailModal(false)}
      />
      <ButtonContainer>
        <Button onClick={() => setShowEmailModal(true)} tracking_id={"subscribe_for_updates"}>
          <div className="single-line-center-center">
            <ButtonText>Subscribe for updates</ButtonText>
            <ButtonIcon><IconLoader iconName={"ArrowRight"} iconType={"arrow"}/></ButtonIcon>
          </div>
        </Button>
      </ButtonContainer>
    </Container>
  )
}

export default GetEmail;

const Container = styled.div`
  margin: 0 auto 0 auto;
  width: 540px;
  z-index: 11;
  @media (max-width: 600px) {
    width: 100%;
  }
`

const ButtonText = styled.span`
  transition: 0.4s ease-in-out;
  transform: translate(10px, 0);
`

const ButtonIcon = styled.div`
  margin-top: 5px;
  transition: 0.4s ease-in-out;
  transform: translate(-10px, 0);
  opacity: 0;
`

const ButtonContainer = styled.div`
  width: max-content;
  margin: auto;
  &:hover ${ButtonIcon} {
    transform: translate(0, 0);
    opacity: 1;
    display: block;
  }

  &:hover ${ButtonText} {
    transform: translate(0, 0);
    display: block;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-left: 0;
    margin-top: 16px;
  }


`

